import {
  ChangeDetectionStrategy,
  Component,
  DestroyRef,
  EventEmitter,
  Input,
  Optional,
  Output,
  forwardRef,
} from '@angular/core';
import { provideControlContainer, provideValueAccessor } from '../utils/form.util';
import { FormGroupDirective } from '@angular/forms';
import { FormGenericComponent } from '../../services/form-generic.abstract';
import { ButtonComponent } from '../../components/atoms/button/button.component';

@Component({
    selector: 'ct-radio-button',
    imports: [ButtonComponent],
    templateUrl: './radio-button.component.html',
    providers: [provideValueAccessor(forwardRef(() => RadioButtonComponent))],
    viewProviders: [provideControlContainer()],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class RadioButtonComponent extends FormGenericComponent {
  @Input() key = '';
  @Input() label = '';
  @Input() value: any;
  @Input() checked: boolean = false;
  @Input() formControlName = '';
  @Output() valueChange = new EventEmitter<any>();

  constructor(
    private readonly destroyRef: DestroyRef,
    @Optional() protected override formGroupDirective: FormGroupDirective
  ) {
    super(formGroupDirective);
  }

  writeValue(value: boolean): void {
    this.checked = value;
    this.triggerMarkCheck();
  }

  onValueChange() {
    // this.checked = true;
    this.setValue(this.value);
    this.valueChange.emit(this.value);
  }

  private setValue(value: any): void {
    this.triggerChange(value);
  }
}
