import { AsyncPipe, NgTemplateOutlet } from '@angular/common';
import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  ContentChild,
  ContentChildren,
  DestroyRef,
  Input,
  QueryList,
  TemplateRef,
  inject,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { BehaviorSubject, filter, skip } from 'rxjs';
import { AccordionComponent } from '../../molecules/accordion/accordion.component';
import { IconComponent } from '../icon/icon.component';

@Component({
    selector: 'ct-accordion-item',
    imports: [IconComponent, AsyncPipe, NgTemplateOutlet],
    templateUrl: './accordion-item.component.html',
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class AccordionItemComponent implements AfterViewInit {
  private readonly destroyRef = inject(DestroyRef);
  @Input({ required: true }) id = '';
  @Input() label = '';
  isOpen = new BehaviorSubject<boolean>(false);

  @ContentChild('header') headerTemplate!: TemplateRef<any>;
  @ContentChildren(AccordionComponent) accordions!: QueryList<AccordionComponent>;

  ngAfterViewInit() {
    this.isOpen
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        skip(1),
        filter(isOpen => !isOpen)
      )
      .subscribe(() => {
        this.accordions.forEach(accordion => {
          accordion.closePanes();
        });
      });
  }

  togglePane() {
    this.isOpen.next(!this.isOpen.getValue());
  }
}
