import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { ComponentType } from '../../../model/components/component-type.type';
import { IconComponent } from '../icon/icon.component';
import { NgClass } from '@angular/common';
import { ButtonComponent } from '../button/button.component';
import { ComponentVariant } from '../../../model/components/component-variant.type';
import { ComponentSize } from '../../../model/components/component-size.type';
/**
 * The alert component is responsible to show alerts for diverse elements. Including `FormErrorComponent` among others.
 */
@Component({
    selector: 'ct-alert',
    imports: [IconComponent, ButtonComponent, NgClass],
    templateUrl: './alert.component.html',
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class AlertComponent {
  @Input() dismissable: boolean = false;
  @Input() fullSize: boolean = true;
  @Input({ required: true }) text = '';
  @Input() type: ComponentType = 'primary';
  @Input() variant: ComponentVariant = 'solid';
  @Input() size: ComponentSize = 'base';
  @Input() withIcon: boolean = true;
  @Output() dismiss = new EventEmitter<void>();

  get icon(): string {
    switch (this.type) {
      case 'primary':
      case 'secondary':
      case 'neutral':
        return 'information-circle';
      case 'error':
        return 'exclamation-triangle';
      case 'warning':
        return 'exclamation-circle';
      case 'success':
        return 'check-circle';
      default:
        return '';
    }
  }
}
