import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  HostListener,
  Input,
  Optional,
  Output,
  forwardRef,
} from '@angular/core';
import { FormGroupDirective } from '@angular/forms';
import { AsyncPipe, NgClass } from '@angular/common';
import { provideControlContainer, provideValueAccessor } from '../utils/form.util';
import { FormGenericComponent } from '../../services/form-generic.abstract';
import { FormErrorComponent } from '../error/error.component';
import { LabelComponent } from '../label/label.component';
import { ComponentPosition } from '../../model/components/component-position.type';
import { IconComponent } from '../../components/atoms/icon/icon.component';
import { ComponentDisposition } from '../../model/components/component-disposition.enum';
import { BehaviorSubject } from 'rxjs';

@Component({
    selector: 'ct-input',
    imports: [NgClass, AsyncPipe, LabelComponent, IconComponent, FormErrorComponent],
    templateUrl: './input.component.html',
    providers: [provideValueAccessor(forwardRef(() => InputComponent))],
    viewProviders: [provideControlContainer()],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class InputComponent extends FormGenericComponent {
  @Input() formControlName!: string;
  @Input() key = '';
  @Input() type: 'password' | 'text' | 'number' = 'text';
  @Input() value: any = '';

  @Input() label = '';
  @Input() icon = '';
  @Input() helper = '';
  @Input() placeholder = '';
  @Input() symbol = '';
  @Input() canDelete = false;
  @Input() fullSize = true;
  @Input() showError = false;
  @Input() min!: number;
  @Input() max!: number;
  @Input() iconPosition: ComponentPosition = 'left';
  @Input() disposition: ComponentDisposition = 'vertical';

  @Output() typed = new EventEmitter<string>();

  showDelete$ = new BehaviorSubject<boolean>(false);

  @HostListener('focusout')
  onFocusOut() {
    this.triggerTouched();
  }

  constructor(@Optional() protected override formGroupDirective: FormGroupDirective) {
    super(formGroupDirective);
  }

  input(event: Event) {
    const value = (event.target as HTMLInputElement).value;
    if (this.canDelete) {
      this.showDelete$.next(!!value);
    }
    this.setValue(value);
  }

  clearValue() {
    this.showDelete$.next(false);
    this.triggerTouched();
    this.setValue(null);
  }

  setValue(value: any, propagate = true) {
    this.value = value;
    if (propagate) {
      this.triggerChange(value);
      this.typed.emit(value);
    }
  }

  writeValue(value: any) {
    if (value && this.canDelete) this.showDelete$.next(true);
    this.triggerMarkCheck();
    this.setValue(value, false);
  }
}
