import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { IconComponent } from '../icon/icon.component';
import { ComponentSize } from '../../../model/components/component-size.type';
import { NgClass } from '@angular/common';
import { ComponentType } from '../../../model/components/component-type.type';
import { ComponentVariant } from '../../../model/components/component-variant.type';
import { ComponentPosition } from '../../../model/components/component-position.type';
/**
 * This is the ButtonComponent, widely used among other components
 */
@Component({
    selector: 'ct-button',
    imports: [NgClass, IconComponent],
    templateUrl: './button.component.html',
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ButtonComponent {
  @Input() text?: string;
  @Input() icon?: string;
  @Input() iconPosition?: ComponentPosition | string = 'left';
  @Input() role: 'button' | 'submit' | 'reset' = 'button';
  @Input() disabled = false;

  @Input() fullSize: boolean = false;
  @Input() size: ComponentSize = 'base';
  @Input() type: ComponentType = 'primary';
  @Input() variant: ComponentVariant = 'solid';

  get cssClassmap(): { [className: string]: boolean } {
    return {
      [`text-on-${this.type} dark:text-on-${this.type}-dark`]:
        this.variant === 'solid' || this.variant === 'gradient' || this.variant === 'flat',
      [`text-${this.type} dark:text-${this.type}-dark hover:text-${this.type}-variant dark:hover:text-${this.type}-variant-dark`]:
        this.variant === 'blank' || this.variant === 'outline',
      [`bg-${this.type} dark:bg-${this.type}-dark hover:bg-${this.type}-variant hover:dark:bg-${this.type}-variant-dark`]:
        this.variant === 'solid',
      [`bg-${this.type}/25 dark:bg-${this.type}-dark/25 hover:bg-${this.type}-variant/25 hover:dark:bg-${this.type}-variant-dark/25`]:
        this.variant === 'flat',
      [`from-${this.type}-variant from-30% to-${this.type} bg-gradient-to-tl hover:bg-gradient-to-br dark:from-${this.type}-variant-dark dark:to-${this.type}-dark`]:
        this.variant === 'gradient',
      [`border border-2 border-${this.type} hover:border-${this.type}-variant dark:border-${this.type}-dark`]:
        this.variant === 'outline',
      'px-3 py-2': (this.size === 'xs' || this.size === 'sm') && !this.hasJustIcon(),
      'px-5 py-2.5': (this.size === 'base' || this.size === 'lg') && !this.hasJustIcon(),
      'px-6 py-3': this.size === 'xl' && !this.hasJustIcon(),
      'p-1': this.hasJustIcon() && this.variant !== 'blank',
      'text-xs': this.size === 'xs',
      'text-sm': this.size === 'sm' || this.size === 'base',
      'text-base': this.size === 'lg' || this.size === 'xl',
      'w-full grow': this.fullSize,
      'opacity-50 hover:none': this.disabled,
    };
  }

  hasJustIcon(): boolean {
    return this.icon !== undefined && this.text === undefined;
  }
}
