import { NgClass } from '@angular/common';
import { ChangeDetectionStrategy, Component, Input, OnInit } from '@angular/core';
import { ComponentSize } from '../../../model/components/component-size.type';
import { ComponentVariant } from '../../../model/components/component-variant.type';

@Component({
    selector: 'ct-icon',
    imports: [NgClass],
    templateUrl: './icon.component.html',
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class IconComponent implements OnInit {
  @Input() icon = '';
  @Input() fill = 'currentColor';
  @Input() strokeWidth = 1.5;
  @Input() strokeColor = 'currentColor';
  @Input() size: ComponentSize = 'base';
  @Input() variant: ComponentVariant = 'outline';

  ngOnInit(): void {
    this.strokeColor = this.getFormattedColor(this.strokeColor);
    this.fill = this.getFormattedColor(this.fill);
  }

  get iconPath(): string {
    const icon = this.variant === 'solid' ? `${this.icon}-solid` : this.icon;
    return `/assets/images/icons.svg#${icon}`;
  }

  private getFormattedColor(color: string): string {
    if (color.startsWith('--')) {
      const rgbValue = getComputedStyle(document.documentElement).getPropertyValue(color).trim();
      return `rgb(${rgbValue})`;
    }
    return color;
  }
}
