import {
  AfterContentInit,
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  DestroyRef,
  EventEmitter,
  Input,
  Optional,
  Output,
  QueryList,
  forwardRef,
} from '@angular/core';
import { FormGenericComponent } from '../../services/form-generic.abstract';
import { provideControlContainer, provideValueAccessor } from '../utils/form.util';
import { FormGroupDirective } from '@angular/forms';
import { RadioButtonComponent } from '../radio-button/radio-button.component';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { LabelComponent } from '../label/label.component';
import { FormErrorComponent } from '../error/error.component';

@Component({
    selector: 'ct-radio-group',
    imports: [LabelComponent, FormErrorComponent],
    templateUrl: './radio-group.component.html',
    providers: [provideValueAccessor(forwardRef(() => RadioGroupComponent))],
    viewProviders: [provideControlContainer()],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class RadioGroupComponent extends FormGenericComponent implements AfterContentInit {
  @Input() formControlName = '';
  @Input() key = '';
  @Input() label = '';
  @Input() value: any = '';
  @Input() helper = '';
  @Input() showError = false;
  @Output() selected = new EventEmitter<any>();

  @ContentChildren(RadioButtonComponent) radios!: QueryList<RadioButtonComponent>;

  constructor(
    private readonly destroyRef: DestroyRef,
    @Optional() protected override formGroupDirective: FormGroupDirective
  ) {
    super(formGroupDirective);
  }

  ngAfterContentInit(): void {
    if (this.value) this.writeValue(this.value);
    this.radios.forEach(radio =>
      radio.valueChange.pipe(takeUntilDestroyed(this.destroyRef)).subscribe(value => this.setValue(value))
    );
  }

  writeValue(value: any) {
    this.value = value;
    if (this.radios) this.updateRadios(value);
  }

  setValue(value: any) {
    this.value = value;
    this.updateRadios(value);
    this.triggerChange(value);
    this.selected.emit(value);
  }

  private updateRadios(value: any): void {
    this.radios.forEach(radio => {
      radio.writeValue(radio.value === value);
    });
  }
}
