import {
  DestroyRef,
  Directive,
  ElementRef,
  EventEmitter,
  HostListener,
  Input,
  Output,
  TemplateRef,
  ViewContainerRef,
} from '@angular/core';
import { Overlay, OverlayRef, ConnectedPosition, FlexibleConnectedPositionStrategy } from '@angular/cdk/overlay';
import { TemplatePortal } from '@angular/cdk/portal';
import { ComponentDisposition } from '../model/components/component-disposition.enum';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { debounceTime, fromEvent } from 'rxjs';

@Directive({
  selector: '[ctOverlay]',
  standalone: true,
})
export class OverlayDirective {
  private verticalPositions: { [key: string]: ConnectedPosition } = {
    bottomStart: {
      originX: 'start',
      originY: 'bottom',
      overlayX: 'start',
      overlayY: 'top',
      panelClass: 'vertical-overlay',
    },
    topStart: {
      originX: 'start',
      originY: 'top',
      overlayX: 'start',
      overlayY: 'bottom',
      panelClass: 'vertical-overlay',
    },
    bottomEnd: {
      originX: 'end',
      originY: 'bottom',
      overlayX: 'end',
      overlayY: 'top',
      panelClass: 'vertical-overlay',
    },
    topEnd: {
      originX: 'end',
      originY: 'top',
      overlayX: 'end',
      overlayY: 'bottom',
      panelClass: 'vertical-overlay',
    },
  };
  private horizontalPositions: { [key: string]: ConnectedPosition } = {
    bottomStart: {
      originX: 'end',
      originY: 'bottom',
      overlayX: 'start',
      overlayY: 'bottom',
      offsetY: 7,
    },
    topStart: {
      originX: 'start',
      originY: 'bottom',
      overlayX: 'end',
      overlayY: 'bottom',
    },
  };
  private overlayRef: OverlayRef | null = null;
  @Input('ctOverlay') contentTemplate!: TemplateRef<any>;
  @Input('ctOverlayPosition') position: ComponentDisposition = 'horizontal';
  @Input('ctOverlayCloseOnClick') closeOnClick: boolean = false;
  @Output() opened = new EventEmitter<void>();
  @Output() closed = new EventEmitter<void>();

  constructor(
    private overlay: Overlay,
    private elementRef: ElementRef,
    private viewContainerRef: ViewContainerRef,
    private readonly destroyRef: DestroyRef
  ) {}

  @HostListener('click')
  show(): void {
    this.openDropdown();
    this.opened.emit();
  }

  private openDropdown(): void {
    if (!this.overlayRef) {
      this.overlayRef = this.overlay.create({
        positionStrategy: this.getOverlayPosition(),
      });

      this.overlayRef
        .outsidePointerEvents()
        .pipe(takeUntilDestroyed(this.destroyRef), debounceTime(100))
        .subscribe(() => {
          this.closeDropdown();
          this.closed.emit();
        });

      fromEvent(this.overlayRef.hostElement, 'click')
        .pipe(takeUntilDestroyed(this.destroyRef))
        .subscribe(() => {
          if (this.closeOnClick) this.closeDropdown();
        });
    }

    if (this.contentTemplate && !this.overlayRef.hasAttached()) {
      const portal = new TemplatePortal(this.contentTemplate, this.viewContainerRef);
      this.overlayRef.attach(portal);
    }
  }

  private closeDropdown(): void {
    if (this.overlayRef?.hasAttached()) {
      this.overlayRef.detach();
    }
  }

  private getOverlayPosition(): FlexibleConnectedPositionStrategy {
    const positions = this.position === 'horizontal' ? this.horizontalPositions : this.verticalPositions;
    return this.overlay
      .position()
      .flexibleConnectedTo(this.elementRef)
      .withPositions(Object.keys(positions).map(positionKey => positions[positionKey]));
  }
}
