import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  DestroyRef,
  Input,
  OnDestroy,
  Optional,
  QueryList,
  SkipSelf,
  inject,
} from '@angular/core';
import { IconComponent } from '../../atoms/icon/icon.component';
import { AccordionItemComponent } from '../../atoms/accordion-item/accordion-item.component';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { debounceTime, filter, skip } from 'rxjs';
import { AsyncPipe } from '@angular/common';
import { AccordionRegistryService } from '../../../services/accordion-registry.service';

@Component({
    selector: 'ct-accordion',
    imports: [AsyncPipe, IconComponent],
    templateUrl: './accordion.component.html',
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class AccordionComponent implements AfterViewInit, OnDestroy {
  private readonly destroyRef = inject(DestroyRef);
  private readonly accordionRegistryService = inject(AccordionRegistryService);
  @Input() label = '';
  @Input({ required: true }) id!: string;
  @Input() policy: 'collapse' | 'open' = 'collapse';
  @Input() gap: number = 0;

  @ContentChildren(AccordionItemComponent) accordionItems!: QueryList<AccordionItemComponent>;

  constructor(@Optional() @SkipSelf() protected parent: AccordionItemComponent) {}

  ngAfterViewInit() {
    this.accordionItems.forEach(accordionItem => {
      accordionItem.isOpen
        .pipe(
          takeUntilDestroyed(this.destroyRef),
          skip(1),
          filter(isOpen => isOpen),
          debounceTime(10)
        )
        .subscribe(() => {
          if (this.parent) {
            this.parent?.isOpen.next(true);
          }
          if (this.policy === 'collapse') {
            this.accordionItems.forEach(otherAccordionItem => {
              if (accordionItem.id !== otherAccordionItem.id && otherAccordionItem.isOpen.getValue()) {
                otherAccordionItem.isOpen.next(false);
              }
            });
          }
        });
    });
    this.accordionRegistryService.register(this.id, this);
  }

  ngOnDestroy() {
    this.accordionRegistryService.unregister(this.id);
  }

  closePanes() {
    this.accordionItems.forEach(accordionItem => accordionItem.isOpen.next(false));
  }
}
