import {
  AfterContentInit,
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  DestroyRef,
  inject,
  Input,
  OnChanges,
  QueryList,
  SimpleChanges,
} from '@angular/core';
import { TabButtonComponent } from '../../atoms/tab-button/tab-button.component';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { TabPanelDirective } from '../../../directives/tab-panel.directive';
import { NgClass, NgTemplateOutlet } from '@angular/common';

@Component({
    selector: 'ct-tab-group',
    imports: [NgClass, NgTemplateOutlet],
    templateUrl: './tab-group.component.html',
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class TabGroupComponent implements AfterContentInit, OnChanges {
  private destroyRef = inject(DestroyRef);
  @Input() border = true;
  @Input() activeTab = '';

  @ContentChildren(TabButtonComponent) buttons!: QueryList<TabButtonComponent>;
  @ContentChildren(TabPanelDirective) panels!: QueryList<TabPanelDirective>;

  ngOnChanges(changes: SimpleChanges) {
    if (changes['activeTab'].previousValue !== this.activeTab) {
      this.setActiveTab(this.activeTab);
    }
  }

  ngAfterContentInit(): void {
    this.setActiveTab(this.activeTab);
    this.buttons.forEach(button =>
      button.clicked.pipe(takeUntilDestroyed(this.destroyRef)).subscribe(key => this.setActiveTab(key))
    );
  }

  setActiveTab(key: string): void {
    if (this.panels && this.buttons) {
      this.panels.forEach(panel => {
        if (panel.ctTabPanel === key) {
          panel.show();
          this.getTabButtonTrigger(panel.ctTabPanel)!.isActive.set(true);
        } else {
          this.getTabButtonTrigger(panel.ctTabPanel)!.isActive.set(false);
          panel.hide.next(true);
        }
      });
    }
  }

  private getTabButtonTrigger(key: string): TabButtonComponent {
    return this.buttons.find(panel => panel.key === key) as TabButtonComponent;
  }
}
