import {
  ChangeDetectionStrategy,
  Component,
  DestroyRef,
  EventEmitter,
  Input,
  Output,
  forwardRef,
  inject,
} from '@angular/core';
import { FormGenericComponent } from '../../services/form-generic.abstract';
import { ComponentPosition } from '../../model/components/component-position.type';
import { FormGroupDirective } from '@angular/forms';
import { FormErrorComponent } from '../error/error.component';
import { LabelComponent } from '../label/label.component';
import { AsyncPipe, NgClass } from '@angular/common';
import { IconComponent } from '../../components/atoms/icon/icon.component';
import { FormOptionBase } from '../../model/forms/form-base.model';
import { OptionComponent } from '../option/option.component';
import { provideControlContainer, provideValueAccessor } from '../utils/form.util';
import { InputComponent } from '../input/input.component';
import { BehaviorSubject, Subject, debounceTime } from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { ComponentDisposition } from '../../model/components/component-disposition.enum';

@Component({
    selector: 'ct-autocomplete',
    imports: [IconComponent, FormErrorComponent, LabelComponent, OptionComponent, InputComponent, NgClass, AsyncPipe],
    templateUrl: './autocomplete.component.html',
    providers: [provideValueAccessor(forwardRef(() => AutocompleteComponent))],
    viewProviders: [provideControlContainer()],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class AutocompleteComponent extends FormGenericComponent {
  private readonly destroyRef = inject(DestroyRef);
  @Input() formControlName!: string;
  @Input({ required: true }) key = '';
  @Input({ required: true }) options: FormOptionBase[] = [];
  @Input() label = '';
  @Input() icon = '';
  @Input() helper = '';
  @Input() placeholder = '';
  @Input() value: any = '';
  @Input() inputValue = '';
  @Input() iconPosition: ComponentPosition = 'left';
  @Input() disposition: ComponentDisposition = 'vertical';
  @Input() fullSize = true;
  @Input() multiple = false;
  @Input() showError = false;

  @Output() typed = new EventEmitter<string>();
  @Output() selected = new EventEmitter<any>();

  focusOut$ = new Subject<any>();
  inputValue$ = new BehaviorSubject<string>('');

  constructor(formGroupDirective: FormGroupDirective) {
    super(formGroupDirective);

    this.focusOut$.pipe(takeUntilDestroyed(this.destroyRef), debounceTime(150)).subscribe(() => {
      this.options = [];
      this.triggerTouched();
      this.triggerMarkCheck();
    });
  }

  input(value: string) {
    if (!value) this.options = [];
    if (this.value && !this.multiple) {
      this.setValue(null);
      this.inputValue$.next('');
      return;
    }
    if (this.multiple) {
      this.inputValue$.next(value);
    }
    this.typed.emit(value);
  }

  setValue(value: any, propagate = true) {
    this.value = value;

    if (propagate) {
      this.triggerChange(value);
      this.selected.emit(value);
    }

    if (value) {
      const selectedOption = this.options.find(option => option.key === value)!;
      if (!this.multiple) this.inputValue$.next(selectedOption ? selectedOption.label : this.placeholder);
      else {
        const currentWords = this.inputValue$.getValue().split(' ');
        // replacing the last word with the current 'label' value
        currentWords[currentWords.length - 1] = selectedOption ? selectedOption.label : this.placeholder;
        this.inputValue$.next(currentWords.join(' '));
      }
    }
  }

  writeValue(value: any) {
    this.triggerMarkCheck();
    this.setValue(value, false);
  }
}
