import {
  ChangeDetectionStrategy,
  Component,
  DestroyRef,
  EventEmitter,
  Input,
  Optional,
  Output,
  forwardRef,
} from '@angular/core';
import { FormGenericComponent } from '../../services/form-generic.abstract';
import { FormGroupDirective } from '@angular/forms';
import { ComponentDisposition } from '../../model/components/component-disposition.enum';
import { LabelComponent } from '../label/label.component';
import { provideControlContainer, provideValueAccessor } from '../utils/form.util';

@Component({
    selector: 'ct-toggle',
    imports: [LabelComponent],
    templateUrl: './toggle.component.html',
    providers: [provideValueAccessor(forwardRef(() => ToggleComponent))],
    viewProviders: [provideControlContainer()],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ToggleComponent extends FormGenericComponent {
  @Input() formControlName = '';
  @Input() checked: boolean = false;
  @Input() label = '';
  @Input() key = '';
  @Input() value = '';
  @Input() showError = false;
  @Input() disposition: ComponentDisposition | string = 'horizontal';
  @Output() wasChecked = new EventEmitter<boolean>();

  constructor(
    @Optional() protected override formGroupDirective: FormGroupDirective,
    private readonly destroyRef: DestroyRef
  ) {
    super(formGroupDirective);
  }

  toggle() {
    this.checked = !this.checked;
    this.triggerChange(this.checked);
    this.wasChecked.emit(this.checked);
  }

  writeValue(value: any) {
    this.triggerMarkCheck();
    this.checked = value === this.value;
  }
}
