import { ChangeDetectionStrategy, Component, ContentChildren, Input, QueryList } from '@angular/core';
import { RouterLink } from '@angular/router';
import { ComponentSize } from '../../../model/components/component-size.type';
import { IconComponent } from '../icon/icon.component';
import { ButtonComponent } from '../button/button.component';
import { OverlayDirective } from '../../../directives/overlay.directive';
import { ComponentDisposition } from '../../../model/components/component-disposition.enum';
import { MenuComponent } from '../../molecules/menu/menu.component';
import { BehaviorSubject } from 'rxjs';
import { AsyncPipe, NgClass } from '@angular/common';

@Component({
    selector: 'ct-menu-item',
    imports: [RouterLink, NgClass, AsyncPipe, IconComponent, ButtonComponent, OverlayDirective],
    templateUrl: './menu-item.component.html',
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class MenuItemComponent {
  @Input() text = '';
  @Input() link = '';
  @Input({ required: true }) type: string | 'internal' | 'external' = 'internal';
  @Input() size: ComponentSize = 'base';
  @Input() icon = '';
  @Input() iconSize: ComponentSize = 'base';
  @Input() iconAlone = false;
  @Input() iconDirection: ComponentDisposition = 'horizontal';
  @Input() hasSubmenu = false;
  @Input() submenuPosition: ComponentDisposition = 'horizontal';
  @Input() isActive = false;
  isOpen = new BehaviorSubject<boolean>(false);

  @ContentChildren(MenuComponent) menus!: QueryList<MenuComponent>;
}
