import { DestroyRef, Injectable, inject } from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { BehaviorSubject, fromEvent, Observable } from 'rxjs';
import { debounceTime, distinctUntilChanged, map, startWith, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class ScreenSizeService {
  private destroyRef = inject(DestroyRef);
  private screenSizeSubject = new BehaviorSubject<string>(this.getScreenSize());
  screenSize$: Observable<string> = this.screenSizeSubject.asObservable();

  constructor() {
    fromEvent(window, 'resize')
      .pipe(
        distinctUntilChanged(),
        debounceTime(200),
        map(() => this.getScreenSize()),
        startWith(this.getScreenSize()),
        tap(() => console.log('test updated')),
        takeUntilDestroyed(this.destroyRef)
      )
      .subscribe(this.screenSizeSubject);
  }

  private getScreenSize(): string {
    const width = window.innerWidth;
    if (width < 640) return 'xs';
    if (width >= 640 && width < 768) return 'sm';
    if (width >= 768 && width < 1024) return 'md';
    if (width >= 1024 && width < 1280) return 'lg';
    if (width >= 1280 && width < 1536) return 'xl';
    return '2xl';
  }
}
