import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { AlertComponent } from '../../components/atoms/alert/alert.component';
import { ValidationErrors } from '@angular/forms';
import { KeyValuePipe } from '@angular/common';
import { ComponentSize } from '../../model/components/component-size.type';
import { IconComponent } from '../../components/atoms/icon/icon.component';

@Component({
    selector: 'ct-form-alert',
    imports: [AlertComponent, KeyValuePipe, IconComponent],
    templateUrl: './error.component.html',
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class FormErrorComponent {
  @Input({ required: true }) errors!: ValidationErrors;
  @Input() size: ComponentSize = 'base';
  @Input() hasErrors = false;
  @Input() fullSize = false;
  @Input() helper = '';
  @Input() errorMessages: { [key: string]: string } = {};
}
