import { Injectable } from '@angular/core';
import { AccordionComponent } from '../components/molecules/accordion/accordion.component';
import { AccordionItemComponent } from '../components/atoms/accordion-item/accordion-item.component';

@Injectable({ providedIn: 'any' })
export class AccordionRegistryService {
  private accordions = new Map<string, AccordionComponent>();

  register(id: string, element: AccordionComponent): void {
    this.accordions.set(id, element);
  }

  get(id: string): AccordionComponent | undefined {
    return this.accordions.get(id);
  }

  unregister(id: string): void {
    this.accordions.delete(id);
  }

  getItem(accordionId: string, itemId: string): AccordionItemComponent {
    const accordionObject = this.accordions.get(accordionId) as AccordionComponent;
    return accordionObject.accordionItems.find(item => item.id === itemId) as AccordionItemComponent;
  }

  openItem(accordionId: string, itemId: string) {
    this.getItem(accordionId, itemId)?.isOpen.next(true);
  }

  closeItem(accordionId: string, itemId: string) {
    this.getItem(accordionId, itemId)?.isOpen.next(false);
  }
}
