import {
  ChangeDetectionStrategy,
  Component,
  DestroyRef,
  ElementRef,
  EventEmitter,
  forwardRef,
  Input,
  OnChanges,
  OnInit,
  Optional,
  Output,
  SimpleChanges,
  ViewChild,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { ButtonComponent } from '../../components/atoms/button/button.component';
import { IconComponent } from '../../components/atoms/icon/icon.component';
import { FormOptionBase } from '../../model/forms/form-base.model';
import { OptionComponent } from '../option/option.component';
import { Subject, debounceTime } from 'rxjs';
import { FormGroupDirective } from '@angular/forms';
import { LabelComponent } from '../label/label.component';
import { AlertComponent } from '../../components/atoms/alert/alert.component';
import { provideControlContainer, provideValueAccessor } from '../utils/form.util';
import { FormGenericComponent } from '../../services/form-generic.abstract';
import { FormErrorComponent } from '../error/error.component';
import { ComponentDisposition } from '../../model/components/component-disposition.enum';
import { OverlayDirective } from '../../directives/overlay.directive';

@Component({
    selector: 'ct-select',
    imports: [
        OptionComponent,
        ButtonComponent,
        IconComponent,
        LabelComponent,
        AlertComponent,
        FormErrorComponent,
        OverlayDirective,
    ],
    templateUrl: './select.component.html',
    styles: ['vertical-overlay { @apply w-full; }'],
    providers: [provideValueAccessor(forwardRef(() => SelectComponent))],
    viewProviders: [provideControlContainer()],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class SelectComponent extends FormGenericComponent implements OnInit, OnChanges {
  focusOut$ = new Subject<any>();
  isOpen = false;
  option = '';

  @Input() formControlName!: string;
  @Input() key = '';
  @Input({ required: true }) options: FormOptionBase[] = [];
  @Input() label = '';
  @Input() helper = '';
  @Input() placeholder = '';
  @Input() value: any = '';
  @Input() disposition: ComponentDisposition = 'vertical';
  @Input() fullSize: boolean = true;
  @Input() showError: boolean = false;

  @Output() selected = new EventEmitter<any>();

  @ViewChild('triggerElement') trigger!: ElementRef<HTMLButtonElement>;
  @ViewChild('optionsPanel') panel!: ElementRef<HTMLDivElement>;

  constructor(
    @Optional() protected override formGroupDirective: FormGroupDirective,
    private readonly destroyRef: DestroyRef
  ) {
    super(formGroupDirective);
  }

  ngOnInit(): void {
    this.selectOption(this.value, false);
    this.focusOut$.pipe(takeUntilDestroyed(this.destroyRef), debounceTime(200)).subscribe(() => {
      this.triggerMarkCheck();
      this.close();
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['options']) this.selectOption(this.value, false);
  }

  toggle(): void {
    this.isOpen = !this.isOpen;
    if (!this.isOpen) {
      this.triggerTouched();
    }
  }

  close(): void {
    this.isOpen = false;
    this.triggerTouched();
  }

  selectOption(key: any, propagate = true) {
    const selectedOption = this.options.find(option => option.key === key)!;
    this.option = key === null ? this.placeholder : selectedOption?.label;
    if (propagate) {
      this.selected.emit(key);
      this.triggerChange(selectedOption?.key ?? null);
      this.close();
    }
  }

  writeValue(value: any) {
    this.triggerMarkCheck();
    this.selectOption(value, false);
  }
}
