import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  HostListener,
  Input,
  OnInit,
  Optional,
  forwardRef,
  inject,
} from '@angular/core';
import { AsyncPipe, DecimalPipe, NgClass } from '@angular/common';
import { FormGroupDirective } from '@angular/forms';
import { InputComponent } from '../input/input.component';
import { provideControlContainer, provideValueAccessor } from '../utils/form.util';
import { integerValidator } from '../utils/validators.util';
import { LabelComponent } from '../label/label.component';
import { IconComponent } from '../../components/atoms/icon/icon.component';
import { FormErrorComponent } from '../error/error.component';

@Component({
    selector: 'ct-input-number',
    imports: [NgClass, AsyncPipe, LabelComponent, IconComponent, FormErrorComponent],
    templateUrl: '../input/input.component.html',
    providers: [provideValueAccessor(forwardRef(() => InputNumberComponent)), DecimalPipe],
    viewProviders: [provideControlContainer()],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class InputNumberComponent extends InputComponent implements OnInit, AfterViewInit {
  private readonly decimalPipe = inject(DecimalPipe);
  private rawValue!: number;
  @Input() validate: 'integer' | 'credit-card' = 'integer';

  constructor(@Optional() protected override formGroupDirective: FormGroupDirective) {
    super(formGroupDirective);
  }

  @HostListener('focusout')
  override onFocusOut() {
    this.formatNumber();
    this.triggerTouched();
  }

  @HostListener('focusin')
  onFocusIn() {
    this.reverseFormatNumber();
  }

  ngOnInit(): void {
    if (this.formControl && this.validate === 'integer') this.formControl.addValidators(integerValidator());
  }

  ngAfterViewInit(): void {
    this.formatNumber();
    this.triggerChangeDetection();
  }

  formatNumber() {
    try {
      this.value = this.decimalPipe.transform(this.value, '1.0-2') || '';
    } catch (_error) {
      return;
    }
  }

  reverseFormatNumber(): void {
    this.value = !isNaN(parseFloat(this.value.replace(/[^0-9.-]/g, '')))
      ? parseFloat(this.value.replace(/[^0-9.-]/g, ''))
      : '';
  }
}
