import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { AvatarComponent } from '../../atoms/avatar/avatar.component';
import { ButtonComponent } from '../../atoms/button/button.component';
import { SafeImagePipe } from '../../../pipes/safe-image.pipe';
import { ComponentDisposition } from '../../../model/components/component-disposition.enum';

@Component({
    selector: 'ct-card',
    imports: [AvatarComponent, ButtonComponent, SafeImagePipe],
    templateUrl: './card.component.html',
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class CardComponent {
  @Input() header = '';
  @Input() subHeader = '';
  @Input() title = '';
  @Input() subTitle = '';
  @Input() avatar = '';
  @Input() image = '';
  @Input() disposition: ComponentDisposition = 'vertical';
  @Input() imageThumbnail = '';
  @Input() withActions = false;
  @Output() acceptButton = new EventEmitter<void>();
  @Output() cancelButton = new EventEmitter<void>();
}
