import { ChangeDetectorRef, inject } from '@angular/core';
import { ControlValueAccessor, FormControl, FormGroupDirective } from '@angular/forms';

export abstract class FormGenericComponent implements ControlValueAccessor {
  private onChange: (value: any) => void = () => {};
  private onTouched: () => void = () => {};

  private readonly changeDetectorRef = inject(ChangeDetectorRef);

  abstract formControlName: string;

  constructor(protected formGroupDirective: FormGroupDirective) {}

  hasErrors(formControl: FormControl): boolean {
    return (formControl && /*!formControl.pristine &&*/ formControl.touched && !!formControl.errors) as boolean;
  }

  get formControl() {
    return this.formGroupDirective && (this.formGroupDirective.form.get(this.formControlName) as FormControl);
  }

  registerOnChange(fn: (value: any) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  protected triggerChange(value: any): void {
    if (value && this.formControl) this.formControl.markAsDirty();
    this.onChange(value);
  }

  protected triggerTouched(): void {
    this.onTouched();
  }

  protected triggerMarkCheck(): void {
    this.changeDetectorRef.markForCheck();
  }

  protected triggerChangeDetection(): void {
    this.changeDetectorRef.detectChanges();
  }

  abstract writeValue(value: any): void;
}
